import { useState, useEffect } from 'react';

import '../css/Dividend.css'
function Dividend() {
  const [Dividenddata, setUsers] = useState([]);
  var num=0;
  const getApiData = async () => {

    try {
      const response = await fetch("/dividend")
      const data = await response.json()

      setUsers(data);
      console.log(data)
    }
    catch (err) {
      console.log(err)
    }
  }

  useEffect(() => {
    getApiData();
  }, []);

  return (

    <>



      <table className='mytablebody' >
        <thead>
          <tr>
          <th>Sr.</th>
          <th>COMPANY NAME</th>
          <th>DIVIDEND</th>
          <th>Percentage</th>
          <th>Date Announcement</th>
          <th>Record</th>
          <th>Ex-Dividend</th>

          </tr>
        </thead>

        {/* total */}
        <tfoot>
          <tr>
            <td>Total</td>
            <td></td>
            <td></td>
            <td>{ }</td>
            <td>{ }</td>
            <td>{ }</td>
            <td>{ }</td>
            <td>{ }</td>

          </tr>
        </tfoot>


        {


          <tbody >


            {

               
              Dividenddata.map((item) => {

                num=num+1
                return (

                  < tr >
                  <td>{num}</td>
                    <td>{item['COMPANY NAME']}</td>
                    <td>{item.DIVIDEND}</td>
                    <td>{item.field3}</td>
                    <td>{item.DATE}</td>
                    <td>{item.field5}</td>
                    <td>{item.field6}</td>
                    {/* {
                                                    Object.keys(realprice.response.rates).map((key) => {
                                                        return (
                                                            <td>{key }</td>)
                                                    }
                                                    )} */}




                  </tr>

                )
              })

            }


          </tbody>
        }
      </table>
    </>
  )
}

export default Dividend