import './App.css';
import { useState,useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import{BrowserRouter as Router,Routes,Route} from "react-router-dom"
import Home from './components/Home';
import ChartData from './components/ChartsData';
import Dividend from './components/Dividend';
import News from './components/News'
import Postnews from './Admin/Postnews'
import Allindices from './components/Allindices';
import Ipodata from './components/Ipo';
import Topgainlooser from './components/Topgainlooser';
import Header from './components/Header';
import Fullnews from './components/Fullnews';
import Screenner from './components/Screenner';
import Buyback from './components/Buyback';
import Right_issue from './components/Right_issue';
import Allprice from './components/Allprice';
import Spilit from './components/Spilit';
import Bonus from './components/Bonus';
import MCX from './components/MCX';
import Postdefi from './Admin/Postdefi';
import Getdeflist from './Defination/getdeflist';
import Fulldefination from './Defination/fulldefination';
import HashLoader from "react-spinners/HashLoader"
import Footer from './components/Footer';
import Portfolio from './Portfolio/portfolio';
