import Signin from './users/signin';
import Investedstock from './Portfolio/investedstock';
import Graph from './components/Graph';
import About from './components/About';
import Logout from './components/Logout';
const App=()=> {
  const [loading,setLoading]=useState(false)
  useEffect(()=>{
    setLoading(true)
    setInterval(()=>{
      setLoading(false)
    },1000)
  },[])
  const override = `
  display: block;
  margin: 0 auto;
  border-color: red;
  
`;
 
  return (
    <>
    { loading?
    <div style={{
      "padding-top":"30vh"
      
    }}><HashLoader css={override} size={150} color={"FF4A5F"} loading={loading} speedMultiplier={1.5} /></div>
    
  : <div>
    <Header/>

    
   
   <Routes>
   <Route exact path='/' element={<Home/>}></Route>

     <Route exact path='/dividend' element={<Dividend/>}></Route>
     <Route exact path='/allindices' element={<Allindices/>}></Route>
     <Route exact path='/ipo' element={<Ipodata/>}></Route>
     <Route exact path='/search' element={<ChartData/>}/>
     <Route exact path='/topgainer' element={<Topgainlooser/>}/>
     <Route exact path='/news' element={<News/>}/>
     <Route exact path='/postnews' element={<Postnews/>}/>
     <Route exact path='/fullnews' element={<Fullnews/>}/>
     <Route exact path='/screener' element={<Screenner/>}/>
     <Route exact path='/elearn' element={<Getdeflist/>}/>
     <Route exact path='/rightissue' element={<Right_issue/>}/>
     <Route exact path='/buyback' element={<Buyback/>}/>
     <Route exact path='/allprice' element={<Allprice/>}/>
     <Route exact path='/spilit' element={<Spilit/>}/>
     <Route exact path='/bonus' element={<Bonus/>}/>
     <Route exact path='/mcx' element={<MCX/>}/>
     <Route exact path='/postdefination' element={<Postdefi/>}/>

     <Route exact path='/fulldefination' element={<Fulldefination/>}/>
    
     <Route exact path='/buystock' element={<Portfolio/>}/>
     <Route exact path='/signup' element={<Signup/>}/>
     <Route exact path='/login' element={<Signin/>}/>
     <Route exact path='/logout' element={<Logout/>}/>
     <Route exact path='/investedstock' element={<Investedstock/>}/>
     <Route exact path='/graphs' element={<Graph/>}/>
  
     <Route exact path='/about' element={<About/>}/>
   </Routes>
<Footer/>
  </div>
  }
     
   
 
    </>

  );
}
