import React from 'react'
import { useState, useEffect } from 'react';

function MCX() {
    const [MCXdata, setUsers] = useState([]);

    const getApiData = async () => {

        try {
            const response = await fetch("/mcx")
            const data = await response.json()

            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }

    useEffect(() => {
        getApiData();
    }, []);
    return (
        <div>
            
            <table className='mytablebody' >
                <thead>
                    <tr>
                        <th>Symbol</th>

                        <th>Last Price</th>
                        <th>Change</th>
                        <th>Change%</th>
                        <th>High/Low</th>

                        <th>Average Price</th>
                        <th>Volume (in lots)</th>
                        <th>Value (Rs Lakh)</th>

                    </tr>
                </thead>

                {/* total */}
                <tfoot>
                    <tr>
                        <td>Total</td>
                        <td></td>
                        <td></td>
                        <td>{ }</td>
                        <td>{ }</td>
                        <td>{ }</td>
                        <td>{ }</td>
                        <td>{ }</td>

                    </tr>
                </tfoot>


                {


                    <tbody >


                        {


                            MCXdata.map((item) => {


                                return (

                                    < tr >
                                        <td>{item.Symbol}</td>

                                        <td>{item.Last}</td>
                                        <td>{item.Change}</td>
                                        <td>{item.Chg}</td>
                                        <td>{item.High}</td>
                                        <td>{item.Average}</td>
                                        <td>{item.Volume}</td>
                                        <td>{item.Value}</td>
                                        {/* {
                                                    Object.keys(realprice.response.rates).map((key) => {
                                                        return (
                                                            <td>{key }</td>)
                                                    }
                                                    )} */}




                                    </tr>

                                )
                            })

                        }


                    </tbody>
                }
            </table>
        </div>
    )
}




export default MCX