import React from 'react'
import { useState, useEffect } from 'react'
import '../css/News.css'
import { Link} from 'react-router-dom'
function News() {
  

    const [news, setNews] = useState([])
    const Mynews = () => {

        fetch('/newsapi')
            .then(response => {
                return response.json()
            })
            .then(data => {
                setNews(data)
            })

    }
    console.log(news)







    useEffect(() => {
        Mynews()

    }, [])

    return (

        <>
            <marquee style={{ backgroundColor: "red", "paddingLeft": "10px","font-size":"40px","scrollamount":"2" }}>Blog and Post</marquee>



            {
                
                news.map((item) => {
                    const A=item.news
                    return (

                        <div className='mynewsdiv'>
                            <div className='img'><img className='mymg' src={item.photo} alt="this is" /></div>
                            <div className='contnt'>
                                <h4>{item.heading}</h4>
                                <p className='news-main'>
                                    {A!=undefined ? A.substring(0,200):"hii"}
                                </p>
                                
                                <Link to={`/fullnews?_id=${item._id}`  }>See more</Link>
                                
                            </div>
                        </div>
                    )
                })}










        </>)
}

export default News