/* App.js */
import React, { useState, useEffect } from "react";
import '../css/ChartData.css'
import { Line } from "react-chartjs-2";
import zoomPlugin from 'chartjs-plugin-zoom';
import Chart from 'chart.js/auto'
Chart.register(zoomPlugin);




const ChartData = () => {
  const [users, setUsers] = useState([]);
  const [name, setname] = useState('MSFT')
  const [clickname, setclickname] = useState('MSFT')

  const setnames = () => {
    setname(clickname)
  }

  const Stockdata = (e) => {
    setclickname(e.target.value)
  }

  // for chart
  //for Data
  const data = {
    // x-axis label values
    labels: users.map(xdata => {
      return xdata.datetime
    }),
    datasets: [

      {
        label: "high price",
        // y-axis data plotting values
        data: users.map(ydata => {
          return ydata.high
        }),
        fill: false,
        borderWidth: 4,
        backgroundColor: ["red"],
        borderColor: 'red',
        responsive: true,
        tension: 0.1
      },
      {
        label: "low price",
        // y-axis data plotting values
        data: users.map(ydata => {
          return ydata.low
        }),
        fill: false,
        borderWidth: 4,
        backgroundColor: ["green"],
        borderColor: 'green',
        responsive: true
      },
    ],




  }





  // Function to collect data
  const getApi = async () => {
    try {
      const url = `https://api.twelvedata.com/time_series?symbol=${name}&interval=1month&apikey=2d7db1ecd33348f7bf3e7ff92dc4b3ba&timezone=Asia/Kolkata`

      const response = await fetch(url)
      const data = await response.json()
      console.log(data)
      setUsers(data.values);
    }
    catch (err) {
      console.log(err)
    }
  }

  useEffect(() => {

    getApi();// eslint-disable-next-line


  }, [name]);




  return (
    <>

      <div className='mainsearch'>
        <input className='srchbx' type="search" onChange={Stockdata} placeholder="Search here" />
        <button className='searchbtn' onClick={setnames}  >Search</button>
      </div>


      {!name ? (
        <p>data not found</p>
      ) :
        <div>
          <div  >
         
          <table class="tablea">
          <caption className='tablename'>{name}</caption>
            <tr >
              <th>Date</th>
              <th>High</th>
              <th>Low</th>
              <th>Open</th>
              <th>Close</th>
              <th>Volume</th>


            </tr>

            {
              users.map((item) => {
                return (
                  <tr>
                    <td>{item.datetime}</td>
                    <td>{item.high}</td>
                    <td>{item.low}</td>
                    <td>{item.open}</td>

                    <td>{item.close}</td>
                    <td>{item.volume}</td>


                  </tr>)
              })
            }
          </table>
              </div>
              <div>
          <h3>Graph</h3>
      <Line
        data={data}
        options={{

          plugins: {
            zoom: {
              limits: {
                y: { min: 0, max: 1000 },
                y2: { min: -5, max: 1000 }
              },
              zoom: {
                drag: {
                  enabled: true,
                  borderColor: 'hsl(35, 100%, 60%)',
                  borderWidth: '3',
                  backgroundColor: 'hsl(35, 100%, 60%)',
                  threshold: 1
                },
                wheel: {
                  enabled: true,
                },
                pinch: {
                  enabled: true
                },
                mode: 'y',
              }
            }
          }

        }}


      />;</div>
        </div>

      }
      
    </>
  )

}





export default ChartData;          