import React from 'react'
import { useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import '../css/Fullnews.css'
function Fullnews() {
    const Search = useLocation().search
    const id = new URLSearchParams(Search).get('_id')

    const [news, setNews] = useState([])
    const Mainnews = () => {

        fetch(`/news?_id=${id}`)
            .then(response => {
                return response.json()
            })
            .then(data => {
                setNews(data)
                console.log(data)
            })

    }
    useEffect(() => {
        Mainnews()

    }, [])
    return (
        <>

            {
                news.map((item) => {
                    return (<div>
                        

                       <div>
                       <div className='myheading'>
                       <h2>{item.heading}</h2>
                       </div>
                        <div className='imgdiv'>
                        <img className='mainimg' src={item.photo} alt="not found" />
                        </div>
                       
                       <div className='para'>
                       <p> {item.news} </p>
                       </div>
                       </div>
                        
                        

                      
                    </div>)
                })
            }
        </>
    )
}

export default Fullnews