import React from 'react'
import '../css/Home.css'
import '../css/Search.css'
import kartik from '../images/kartik.png'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image'
import Man from '../images/man.png'
import People from '../images/people.png'
import priyanshu from '../images/2.jpeg'
import Gaurav from '../images/3.jpeg'
import { TickerTape } from "react-ts-tradingview-widgets";
import { useState, useEffect } from 'react';

import { NavLink } from 'react-router-dom'

const Home = () => {
  const [News, SetNews] = useState([])
  const Getnews = async () => {
    try {
      const response = await fetch("/newsapi")
      const data = await response.json()
      SetNews(data)
      console.log(data)


    }
    catch (err) {
      console.log(err)
    }

  }
  useEffect(() => {
    Getnews();
  }, []);
  const styles = {
    parent: {
      fontSize: "1px",
      color: "red",
    },
    link: {
      textDecoration: "line-trough",
    },
    span: {
      color: "darkblue",
    },

  };
  return (


    <>

      <header className="homeimage">
        <TickerTape colorTheme="dark" copyrightStyles={styles}></TickerTape>


      </header>
      <section className="hero">
        <h1 className='maintitle'>Penny Cap</h1>
        <p className='titleslog'>From Penny To Unicorn</p>
        <button className='mysearchbutton' to='/'>Search</button>
      </section>

      <section >

        <div className="hero1">
          {/* <iframe title='live Price' src="https://docs.google.com/spreadsheets/d/e/2PACX-1vSD4xw8LD3kN3BaIkVUuZcWo_9Dt8B63xve1iAe0RwG0K49X8bR2gcm2CKfX8-662uJbyJ4oKoEOPvW/pubhtml?gid=0&amp;single=true&amp;widget=true&amp;headers=false" ></iframe> */}

        </div>

      </section>




      <div className="Blogheading"><p>Latest Post</p></div>


      <section>

        <div className="container-home">

          {

            News.slice(0, 3).map((item) => {
            
              return (

                <div className="card">
                  <div className="card__header">
                    <img
                      src={item.photo}
                      alt="card__image"
                      className="card__image"
                      width={600}
                      height={200}
                    />
                  </div>
                  <div className="card__body">


                    <p>
                      {/* {A!=undefined ? A.substring(0,200):"hii"} */}
                      {item.heading}
                    </p>
                    <NavLink to={`/fullnews?_id=${item._id}`}><span className="tag tag-blue">See</span></NavLink>
                  </div>

                </div>
              )
            })}


        </div>

        <div style={{ display: "flex", justifyContent: "space-around" }}> <NavLink to={`/news`}>SEE ALL</NavLink></div>
      </section>

      <section className='AboutUs'>
        <p>About Us</p>
        <div className='Smalldiv'>
          <div>
            <img src='https://cdn-icons-png.flaticon.com/512/3145/3145740.png' alt="this is " />
            <h3>Learn</h3>
          </div>
          <div>
            <img src='https://cdn-icons-png.flaticon.com/512/1689/1689164.png' alt="this is " />
            <h3>Get Updated</h3>
          </div><div>
            <img src='https://cdn-icons-png.flaticon.com/512/1611/1611179.png' alt="this is " />
            <h3>Invest</h3>
          </div>

        </div>



      </section>


      <Container >
        <Row style={{ paddingTop: "70px" }}>
          <Col >
            <div>
              Pennycap is a platform where people can know about the basic knowledge of the stock martket.
              They can learn and implement their knowledge in real market
            </div>

          </Col>
          <Col xs={1}></Col>
          <Col>
            <Image src={Man} />
          </Col>
        </Row>

        <Row style={{ paddingTop: "70px" }}>
          <Col>
            <Image src={People} />

          </Col>
          <Col xs={1}></Col>
          <Col>
            <div>
              Pennycap provide Virtual Currency so that people can use these coins in no-real market and
              get expericne. All news that effect the market can be seen in the news section.
            </div>
          </Col>
        </Row>
      </Container>









      {/* our team section */}
      <div>
        <h4 style={{textAlign:"center"}}>Our Team </h4>
      <section className='team'>
        
        <div className='team_card'>
          <div>
            <img src={kartik} height={150} alt='this is ' />
            <div className='team_description'>
              <h4>Backend Design </h4>
              <p>In Express Js</p>
            </div>
          </div>
        </div>

    
        <div className='team_card'>
          <div>
            <img src={priyanshu} height={150} alt="this is" />
            <div className='team_description'>
              <h4>Research and UI</h4>
              <p></p>
            </div>
          </div>
        </div><div className='team_card'>
          <div>
            <img src={Gaurav} height={150} alt="this is" />
            <div className='team_description'>
              <h4>Frontend Design</h4>
              <p>HTML and CSS</p>
            </div>
          </div>
        </div>
      </section>
      </div>





    </>)
}


export default Home