import { useState, useEffect } from 'react';

function Right_issue() {

  const [Rightissue, setUsers] = useState([]);

    const getApiData = async () => {
        
        try {
            const response = await fetch("/rightissue" )
            const data = await response.json()
    
            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }
    
    useEffect(() => {
        getApiData();
    }, []);
  return (
    <>
     <div >
        <div >
        
        <table className='tablea'>
        <caption className='tablename'>Right Issue</caption>
          <tr  >
            <th>Company</th>
            <th>Rights Ratio</th>
            <th>FV</th>
            <th>Premium</th>
            <th>DATE</th>
            <th>Record</th>
            <th>Ex-Right</th>
           
          </tr>

          {
            Rightissue.map((item) => {
              return (
                <tr >
                  <td>{item.Company}</td>
                  <td>{item['Rights Ratio']}</td>
                  <td>{item.FV}</td>
                  <td>{item.Premium}</td>
                  <td>{item.DATE}</td>
                  <td>{item.field6}</td>
                  <td>{item.field7}</td>
                  
                </tr>)
            })
          }
        </table>
            </div>
           
        
      </div>
    </>
  )
}

export default Right_issue

