import { useState, useEffect } from 'react';
import '../css/Dividend.css'

function Allindices() {

  const [Mydata, setUsers] = useState([]);

    const getApiData = async () => {
        
        try {
            const response = await fetch("/allindices" )
            const data = await response.json()
    
            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }
    
    useEffect(() => {
        getApiData();
    }, []);
  return (
   <>
   


<table  class="tablea">
<caption id='Moreindi' className='tablename'>Indices</caption>
  <tr  >
    <th>Symbol</th>
    <th>Open</th>
    <th>High</th>
    <th>Low</th>
    <th>Traded Volume(lacs)</th>
    <th>Change(%)</th>
  </tr>

  {
    Mydata.map((item) => {
      return(
      <tr >
        <td>{item.Symbol}</td>
        <td>{item.Open}</td>
        <td>{item.High}</td>
        <td>{item.Low}</td>
        <td>{item['Traded Volume(lacs)']}</td>
        <td>{item['%Change']}</td>
      </tr>)
    })
  }
</table>
   </>
  )
}

export default Allindices