
import '../css/Dividend.css'
import { useState, useEffect } from 'react';

function Ipo() {

  const [Ipodata, setUsers] = useState([]);

  const getApiData = async () => {
      
      try {
          const response = await fetch("/ipo" )
          const data = await response.json()
  
          setUsers(data);
          console.log(data)
      }
      catch (err) {
          console.log(err)
      }
  }
  
  useEffect(() => {
      getApiData();
  }, []);

  return (
   <>
 
    <table className="tablea">
    <caption className='tablename'>IPO</caption>
        <tr  >
          <th>Issuer Company</th>
          <th>Exchange</th>
          <th>Open</th>
          <th>Close</th>
          <th>Lot Size</th>
          <th>Issue Price (Rs)</th>
          <th>Issue Size (Rs Cr)</th>
        </tr>

        {
          Ipodata.map((item) => {
            return(
            <tr  >
              <td>{item['Issuer Company']}</td>
              <td>{item.Exchange}</td>
              <td>{item.Open}</td>
              <td>{item.Close}</td>
              <td>{item['Lot Size']}</td>
              <td>{item['Issue Price (Rs)']}</td>
              <td>{item['Issue Size (Rs Cr)']}</td>
            </tr>)
          })
        }
      </table>

      
   </>
  )
}

export default Ipo