import React from 'react'
import { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import './portfolio.css'
function Portfolio() {
  const [rates, setrates] = useState([]);
  const [isloading, setloading] = useState(true);
  const [show, setShow] = useState(false);
  const [price, setprice] = useState("");
  const [stockname, setstockname] = useState("");
  const [quantity, setquantity] = useState(1);



  const PostData = async (e) => {
    e.preventDefault();

    console.log("ynha se")
    const res = await fetch("/buyed", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        stockname, price, quantity, totalprice: price * quantity //name:name eg
      })

    });
    const data = await res.json();
    if (res.status === 422 || !data) {
      window.alert("Invalid Registestarion");

    }
    else {
      window.alert("Uploaded Suucesfully")
      console.log("Uploaded Succesfully")

    }

  }

  const handleClose = () => setShow(false);
 

  const getApiData = async () => {
    try {
      const response = await fetch("https://api.currencyscoop.com/v1/latest?api_key=9ce39f6c977a6e3f4abb7224441261ee")
      const data = await response.json()

      setrates(data);
      console.log(data)
      setloading(false)
    }
    catch (err) {
      console.log(err)
    }
  }

  useEffect(() => {
    getApiData();
  }, []);
  return (
    <div className='set-upper'>
      {isloading ? (
        <h2>Loading... Wait Please</h2>
      ) : (
       
        <div >
          <h1>Invested STock</h1>
        


          <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>Buy</Modal.Title>
            </Modal.Header>
            <Modal.Body>

              <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                  <Form.Label>Stock Name</Form.Label>
                  <Form.Control
                    value={stockname}
                    type="char"
                    placeholder=""
                    autoFocus
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    value={price}
                    type="int"
                    placeholder="name@example.com"
                    autoFocus
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                  <Form.Label>Quantity</Form.Label>
                  <Form.Control
                    value={quantity}
                    onChange={(e) => {
                      setquantity(e.target.value)
                    }}
                    type="number"
                    placeholder=""
                    autoFocus
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                  <Form.Label>Total Price</Form.Label>
                  <Form.Control
                    value={price * quantity}
                    name="totalprice"
                    type="int"
                    placeholder=""
                    autoFocus
                  />
                </Form.Group>

              </Form>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Cencel
              </Button>
              <Button variant="primary" onClick={PostData}>
                Confirm
              </Button>
            </Modal.Footer>
          </Modal>

          {/* adhdah */}


          {
            Object.keys(rates.response.rates).map((key) => {
              return (
                <div className='pricelist'>

                  <td>{key}:{rates.response.rates[key]}</td>

                  <button className='buybutton' onClick={() => {
                    setstockname(key)
                    setprice(rates.response.rates[key])
                    setShow(true)
                  }}>BUY </button>
                </div>


              )
            })
          }
        </div>
      )
      }
    </div>
  )
}

export default Portfolio