import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {useState} from 'react'
function Signup() {
  const [user, setUser] = useState({
    name: "", email: "",password:"",
  })
  

  const handleInputs = (e) => {

    setUser({
      ...user,
      [e.target.name]: e.target.value
    })
  }
  const Signupuser = async (e) => {
    e.preventDefault();
    const { name,email,password } = user;
 
    console.log("ynha se")
    const res = await fetch("/postdemoaccount", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        name,email,password,  //name:name eg
      })

    });
    const data = await res.json();
    if (res.status === 422 || !data) {
      window.alert("Invalid Registestarion");

    }
    else {
      window.alert("Uploaded Suucesfully")
      console.log("Uploaded Succesfully")

    }

  }
  return (
    <Form>

<Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Name</Form.Label>
        <Form.Control name="name" onChange={handleInputs} type="char" placeholder="Enter name" />
        
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control name="email" onChange={handleInputs} type="email" placeholder="Enter email" />
       
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control name="password" onChange={handleInputs} pasword="password" type="password" placeholder="Password" />
      </Form.Group>
      
      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Confirm Password</Form.Label>
        <Form.Control type="password" placeholder="confirm Password" />
      </Form.Group>
      

      <Button variant="primary" onClick={Signupuser}>
        Submit
      </Button>
    </Form>
  );
}

export default Signup;