import { useState, useEffect } from 'react';



function Bonus() {
    const [Bonusdata, setUsers] = useState([]);

    const getApiData = async () => {
        
        try {
            const response = await fetch("/bonus" )
            const data = await response.json()
    
            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }
    
    useEffect(() => {
        getApiData();
    }, []);



    return (
        <div>
            <div >
                <div >
                    
                    <table className='tablea'>
                    <caption className='tablename'>Bonus</caption>
                        <tr  >
                            <th>COMPANY</th>
                            <th>Bonus Ratio</th>
                            <th>DATE</th>
                            <th>Record</th>
                            <th>EX-Bonus</th>
                            
                        </tr>

                        {
                            Bonusdata.map((item) => {
                                return (
                                    <tr >
                                        <td>{item.COMPANY}</td>
                                        <td>{item['Bonus Ratio']}</td>
                                        <td>{item.DATE}</td>
                                        <td>{item.field4}</td>
                                        <td>{item.field5}</td>
                                        
                                    </tr>)
                            })
                        }
                    </table>
                </div>


            </div>
        </div>
    )
}




export default Bonus