import { useState, useEffect } from 'react';

function Buyback() {

    const [Buybackdata, setUsers] = useState([]);

    const getApiData = async () => {
        
        try {
            const response = await fetch("/buyback" )
            const data = await response.json()
    
            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }
    
    useEffect(() => {
        getApiData();
    }, []);
    return (
        <div>
            <div >
                <div >
                
                    <table className='tablea'>
                    <caption className='tablename'>BuyBack</caption>
                        <tr  >
                            <th>COMPANY Name</th>
                            <th>Record Date</th>
                            <th>Issue Open</th>
                            <th>Issue Close</th>
                            <th>BuyBack Type</th>
                            <th>BuyBack Price (Per Share)</th>
                            <th>Current Market Price</th>
                            <th>Issue Size - Shares (Cr)</th>
                            <th>Issue Size - Amount (Cr)</th>
                        </tr>

                        {
                            Buybackdata.map((item) => {
                                return (
                                    <tr >
                                        <td >{item['Company Name']}</td>
                                        <td>{item['Record Date']}</td>
                                        <td>{item['Issue Open']}</td>
                                        <td>{item['Issue Close']}</td>
                                        <td>{item['Buyback Type']}</td>
                                        <td>{item['BuyBack price (Per Share)']}</td>
                                        <td>{item['Current Market Price']}</td>
                                        <td>{item['Issue Size - Shares (Cr)']}</td>
                                        <td>{item['Issue Size - Amount (Cr)']}</td>
                                    </tr>)
                            })
                        }
                    </table>
                </div>


            </div>
        </div>
    )
}




export default Buyback