import Button from 'react-bootstrap/Button';
import { useState } from 'react';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';

function Signin() {
    const nevigate = useNavigate();
  

  const [user, setUser] = useState({
     email: "",password:"",
  })
  

  const handleInputs = (e) => {

    setUser({
      ...user,
      [e.target.name]: e.target.value
    })
  }
  const Signinuser = async (e) => {
    e.preventDefault();
    const { email,password } = user;
 
    console.log("ynha se")
    const res = await fetch("/signin", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
       email,password,  //name:name eg
      })

    });
    const data = await res.json();
    if (res.status === 400 || !data) {
      window.alert("Invalid Login");

    }
    else {
      nevigate('/investedstock')
      window.alert("Login Suucesfully")
    }
  }
  return (
    <Form>



      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" name="email" onChange={handleInputs} placeholder="Enter email" />
       
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control name="password" type="password" onChange={handleInputs} placeholder="Password" />
      </Form.Group>
      

      

      <Button variant="primary" onClick={Signinuser}>
        Log In
      </Button>

      <a href="/signup">Or Create Account</a>
    </Form>

    
  );
}

export default Signin;