import React,{useState} from 'react'
import {FaAlignJustify} from "react-icons/fa";
import { NavLink } from 'react-router-dom'


import '../css/Header.css'


function Header() {




  return (
    <>
      <div className='header' >
        
        <p className='logo'>PennyCap</p>
        <input type="checkbox" id="menu-bar"  />
          <label for="menu-bar">
          <FaAlignJustify/>
          </label>
        <nav className='Mynavbar'>
       
          
          
          <ul>

            <li><NavLink to="/" className="mylink">Home</NavLink></li>
            <li className="mylink">Graph
            
            <ul>
              <li><NavLink className="Navlink" to="/graphs">Graph</NavLink></li>
            </ul>
                

           
            </li>
            <li className="mylink">Screener
              <ul>
                <li>
                <NavLink className="Navlink" to="/screener">Indian</NavLink>
                </li>
              </ul>
                
             
            </li>
            <li className="mylink">Commodity
              <ul>
                <li>
                <NavLink className="Navlink" to="/ipo">IPO</NavLink>
                </li>
              </ul>
                

              
            </li>
            <li className="mylink">Dividend 
              <ul>
                <li>
                <NavLink className="Navlink" to="/dividend">Dividend </NavLink>
                </li>
              <li>
              <NavLink className="Navlink" to="/mcx">MCX</NavLink>
              </li>
               
              </ul> 
                
              
            </li>
            <li className="mylink">More 
              <ul>
                <li><NavLink className="Navlink" to="/news">News</NavLink></li>
                <li> <NavLink className="Navlink" to="/elearn">E-Learn</NavLink></li>
                <li><NavLink className="Navlink" to="/login">Login</NavLink></li>
                <li><NavLink className="Navlink" to="/logout">Logout</NavLink></li>
                <li> <NavLink className="Navlink" to="/buystock">Buy</NavLink></li>
                <li><NavLink className="Navlink" to="/investedstock">Portfolio</NavLink></li>

              </ul>
                
            
            </li>
 

          </ul>
          
        </nav>
        
      </div>



    </>)
}

export default Header