import React from 'react'
import { useState, useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { Params } from 'react-router-dom'
function Getdeflist() {
  

    const [Def, setDef] = useState([])
    const MyDef = () => {

        fetch('/getdefination')
            .then(response => {
                return response.json()
            })
            .then(data => {
                setDef(data)
            })

    }
    console.log(Def)







    useEffect(() => {
        MyDef()

    }, [])

    return (

        <>
            <marquee style={{ backgroundColor: "red", "paddingLeft": "10px","font-size":"40px","scrollamount":"2" }}>Basic Definition</marquee>



            {
                Def.map((item) => {
                    return (

                        
                            <Link style={{"text-decoration":"none","color":"#084298"}} to={`/fulldefination?_id=${item._id}`}>
                                <div className='mynewsdiv'>
                            <h2>{item.title}</h2>
                            <div className='contnt'>
                                
                                <p className='news-main'>
                                    {/* {item.defination.substring(0, 250)} */}
                                </p>
                                                    
                            </div>
                            </div>
                            </Link>
                       
                    )
                })}










        </>)
}

export default Getdeflist