import React from 'react'
import { useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'

function Fulldefination() {
    const Search = useLocation().search
    const id = new URLSearchParams(Search).get('_id')

    const [Def, setDef] = useState([])
    const Mainnews = () => {

        fetch(`/defination?_id=${id}`)
            .then(response => {
                return response.json()
            })
            .then(data => {
                setDef(data)
                console.log(data)
            })

    }
    useEffect(() => {
        Mainnews()

    }, [])
    return (
        <>

            {
                Def.map((item) => {
                    return (<div>
                        <div>
                       <div className='myheading'>
                       <h2>{item.title}</h2>
                       </div>
                        
                       
                       <div className='para'>
                       <p> {item.explain} </p>
                       </div>
                       </div>
                        

                      
                    </div>)
                })
            }
        </>
    )
}

export default Fulldefination