import React from 'react'
import './investedstock.css'
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
function Investedstock() {
    const nevigate = useNavigate();
    const [myinvest, setmyinvest] = useState([]);
    const [realprice, setrealprice] = useState([]);
    const [isrealpriceloading, setrealpriceloading] = useState(true);
    const [isloading, setloading] = useState(true);
    const [id, setid] = useState("")
    const [sellprice, setsellprice] = useState("")
    const [stockname, setstockname] = useState("")
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    let totalinvested=0;

    const getApidata = async () => {
        try {
            const response = await fetch("https://api.currencyscoop.com/v1/latest?api_key=9ce39f6c977a6e3f4abb7224441261ee")
            const data = await response.json()
            
            setrealprice(data);
            console.log(data)
            setrealpriceloading(false)
        }
        catch (err) {
            console.log(err)
        }
    }


    // send data to backend
    const PostData = async (e) => {
        e.preventDefault();
        setShow(false)
       
        const res = await fetch("/sell", {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            id,sellprice,stockname //name:name eg
          })
    
        }); 
        const data = await res.json();
        if (res.status === 401 || !data) {
          window.alert("Please Login");
          console.log("hello, im ")
          nevigate('/login')

    
        }

        else {
          window.alert("Login Suucesfully")
          
    
        }
    
      }
   

    const getInvestdata = async () => {
        try {
            const response = await fetch("/investstock")
            const data = await response.json()
            if (response.status === 401 || !data) {
                console.log("Hello, Get invest")
                window.alert("Invalid Login");
                nevigate('/login')
          
              }
            else{
                getApidata();
            }
             
            setmyinvest(data);
           
            setloading(false)
        }
        catch (err) {
            console.log(err)
        }
        
    }

    var num = 0;

    useEffect(() => {
        getInvestdata();
        
        // eslint-disable-next-line
    }, []);
    return (



        <>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Confirm</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                            <Form.Label>Id</Form.Label>
                            <Form.Control
                                name="id"
                                value={id}
                                type="char"
                                placeholder=""
                                autoFocus
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                            <Form.Label>Total Price</Form.Label>
                            <Form.Control
                                name="sellprice"
                                value={sellprice}
                                type="int"
                                placeholder=""
                                autoFocus
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                            <Form.Label>Stock Name</Form.Label>
                            <Form.Control
                            name="stockname"
                                value={stockname}
                                type="char"
                                placeholder=""
                                autoFocus
                            />
                        </Form.Group>

                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={PostData}>
                        Confirm
                    </Button>
                </Modal.Footer>
            </Modal>

            {isloading || isrealpriceloading ? (
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            ) : (
                <div>
                        {/* Profile Code */}
                
                        <div class="profile-card">
        <div class="portfolio">Protfolio</div>
        <div class="content">
          <img src="https://t4.ftcdn.net/jpg/02/29/75/83/360_F_229758328_7x8jwCwjtBMmC6rgFzLFhZoEpLobB6L8.jpg" alt="ic"/>


          <div class="amount-div">

            <ul>
                
                        <li>Demo Currency : <span>{myinvest.democurrency}</span></li>
                 
                        <li>Total Invested :  {
               
               myinvest.balences.forEach(element => {
                   totalinvested+=element.totalprice
               })
               
             }<span>{totalinvested}</span></li>
                <li>Total Current value :<span>
                    {
                        myinvest.balences.map((i)=>{
                            console.log(realprice.response.rates[i.stockname] * i.quantity)
                            return(
                            (realprice.response.rates[i.stockname] * i.quantity)
                            )
                            
                        })

                    }
                    </span> </li>
              </ul>
              
                
             
              <ul>
                <li>Total Profit : <span>Not Updated</span></li>
                <li>Total Quantity :<span>Not Updated</span> </li>
                {/* <li>Merge  :<span>287</span> </li> */}
              </ul>
                </div>
          </div>
        
        </div>
        
       

                   
                    <table className='mytablebody' >
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Stock Name</th>
                                <th>Buying Price</th>
                                <th>Quantity</th>
                                <th>Invested Amount</th>
                                <th>Current Amount</th>
                                <th>Profit/Loss</th>
                                <th>Sell</th>
                            </tr>
                        </thead>

                        {/* total */}
                        <tfoot>
                            <tr>
                                <td>Total</td>
                                <td></td>
                                <td></td>
                                <td>{ }</td>
                                <td>{ }</td>
                                <td>{ }</td>
                                <td>{ }</td>
                                <td>{ }</td>

                            </tr>
                        </tfoot>


                        {


                            <tbody >


                                {
                                    myinvest.balences.map((i) => {

                                        num = num + 1
                                        return (

                                            < tr >
                                                <td>{num}</td>
                                                <td>{i.stockname}</td>
                                                <td>{i.price}</td>
                                                <td>{i.quantity}</td>
                                                <td>{i.totalprice}</td>
                                                {/* <td>{i._id}</td> */}

                                                <td>{realprice.response.rates[i.stockname] * i.quantity}</td>
                                                <td>{(realprice.response.rates[i.stockname] * i.quantity) - (i.totalprice)}</td>
                                                {/* {
                                                    Object.keys(realprice.response.rates).map((key) => {
                                                        return (
                                                            <td>{key }</td>)
                                                    }
                                                    )} */}


                                                <td> <Button  onClick={()=>{
                                                    setShow(true)
                                                    setid(i._id)
                                                    setsellprice(realprice.response.rates[i.stockname] * i.quantity)
                                                    setstockname(i.stockname)
                                                }}>
                                                    Sell
                                                </Button></td>

                                            </tr>

                                        )
                                    })

                                }


                            </tbody>
                        }
                    </table>


                </div>
            )
            }
        </>
    )

}

export default Investedstock


