import React, { useEffect, useState } from 'react'

function Spilit() {
    //
    const [Spilitdata, setUsers] = useState([]);

    const getApiData = async () => {
        
        try {
            const response = await fetch("/spilit" )
            const data = await response.json()
    
            setUsers(data);
            console.log(data)
        }
        catch (err) {
            console.log(err)
        }
    }
    
    useEffect(() => {
        getApiData();
    }, []);

    return (
        <div>
            <div >
                <div >
                   
                    <table className='tablea'>
                    <caption className='tablename'>Spilit</caption>
                        <tr  >
                            <th>Company</th>
                            <th>New FV</th>
                            <th>Old FV</th>
                            <th>Split Date</th>
                            
                        </tr>

                        {
                            Spilitdata.map((item) => {
                                return (
                                    <tr >
                                        <td>{item.Company}</td>
                                        <td>{item['New FV']}</td>
                                        <td>{item['Old FV']}</td>
                                        <td>{item['Split Date']}</td>
                                       
                                    </tr>)
                            })
                        }
                    </table>
                </div>


            </div>
        </div>
    )
}




export default Spilit