import React from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useState } from 'react'
function Postdefi() {


  const [user, setUser] = useState({
    title: "", defination: "",
  })
  

  const handleInputs = (e) => {


    setUser({
      ...user,
      [e.target.name]: e.target.value
    })
  }

  





  const PostData = async (e) => {
    e.preventDefault();
    const { title, defination } = user;
 
 
    const res = await fetch("/uploaddefi", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        title, defination,  //name:name eg
      })

    });
    const data = await res.json();
    if (res.status === 422 || !data) {
      window.alert("Invalid Registestarion");

    }
    else {
      window.alert("Uploaded Suucesfully")
      console.log("Uploaded Succesfully")

    }

  }








  return (

    <>
     
   




    <Container>
   
      <Row>
        <Col></Col>
        <Col xs={8}>
        <h2>Post Defination</h2>
        <Form>
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Label>Title</Form.Label>
        <Form.Control type="text" value={user.title} onChange={handleInputs} name="title" placeholder=" " />
      </Form.Group>
      <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
        <Form.Label>Defination</Form.Label>
        <Form.Control name="defination" value={user.defination} onChange={handleInputs} as="textarea" rows={3} />
      
      </Form.Group>
      <Button variant="primary" onClick={PostData}>Post</Button>{' '}
    </Form>
      
        </Col>
        <Col></Col>
      </Row>
     
    </Container>

      

    </>)
}

export default Postdefi