import React from 'react'
import './Postnews.css'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useState } from 'react'
function Postnews() {


  const [user, setUser] = useState({
    heading: "", news: "", photo: ""
  })
  const [selectfilename, setselectfilename] = useState("")
  const [file, setfile] = useState('')
  const [image, setimage] = useState('');

  const fileHandler = (e) => {

    setimage(URL.createObjectURL(e.target.files[0]))
    setfile(e.target.files[0])
    console.log(file)
  }






  const handleInputs = (e) => {


    setUser({
      ...user,
      [e.target.name]: e.target.value
    })
  }

  //pic upload
  const picupload = async () => {
    try {
      const formData = new FormData();
      formData.append('myFile', file)
      formData.append('heading', user.heading)
      formData.append('news', user.news)


      const res = await fetch('/senddata',
        {
          method: 'POST',

          body: formData,

        }

      )
      console.log(res)

      if (res) {
        window.alert("successfulyy Updated")

      }
      else {
        console.log("Not sucess")
      }
    }
    catch (err) {
  
      console.log(err)
    }
  }





  const PostData = async (e) => {
    e.preventDefault();
    const { heading, news } = user;
    picupload()
    console.log("ynha se")
    const res = await fetch("/uploadblog", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        heading, news, file: selectfilename.name  //name:name eg
      })

    });
    const data = await res.json();
    if (res.status === 422 || !data) {
      window.alert("Invalid Registestarion");

    }
    else {
      window.alert("Registarion Suucesfully")
      console.log("Registration Succesfully")

    }

  }








  return (

    <>
      
      <Container>
      <Row>
        <Col></Col>
        <Col xs={12}>
        <Form>
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Label>Headline</Form.Label>
        <Form.Control type="text" value={user.heading} onChange={handleInputs} name="heading" placeholder=" " />
      </Form.Group>
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Label>Choose File</Form.Label>
        <Form.Control type="file" value={user.file} onChange={fileHandler} name="myFile" placeholder=" " />
      </Form.Group>
      <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
        <Form.Label>Example textarea</Form.Label>
        <Form.Control name="news" value={user.news} onChange={handleInputs} as="textarea" rows={3} />
      </Form.Group>
      <Button variant="primary" onClick={picupload}>Post</Button>{' '}
    </Form>
        </Col>
        <Col></Col>
      </Row>
      
    </Container>
    </>)
}

export default Postnews